@import "./animations.scss";

/* Whole Page Styles */
:root {
    --h2-offset: calc(100vw / 20);

    --full-dim: calc(1.25 * max(100vw, 100vh));

    --small-device-width: 575.98px;
}

@media (max-width: 575.98px /*var(--small-device-width)*/) { 
    :root {
        --h2-offset: 0;
    }
}

.no-transition-on-load * {
    transition: none !important;
}

body {
    box-sizing: border-box;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 0;
    color: white;
    font-family: monospace;
    background: linear-gradient(45deg, #831100, #8f1212, #917312, #797a11, #0e641c, #0d5d61, #160f70, #6b0e74, #5a0063);
    background-size: 1500% 1500%;
    animation: rainbow 30s ease infinite;
}

#pureFullPage {
    width: 100vw;
}

.page {
    position: relative;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* Header Page */
header {

    text-align: center;
    display: flex;
    justify-content: center;
}

header > .name-text {
    animation: blur-loop var(--pulse-duration) infinite forwards var(--timing-function);
    width: fit-content;
    padding: 1em;
    border-left: white 12px solid;
    border-right: white 12px solid;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 22px;
}

@keyframes arrow-grow {
    from {
        top: -112px;
    } 
    to {
        top: 0px;
    }
}

@keyframes arrow-flicker {
    from {
      visibility: visible;
    }
    29% {
       visibility: visible;
    }
    30% {
       visibility: hidden;
    }
    31% {
       visibility: hidden;
    }
    32% {
      visibility: visible;
    }
    33% {
       visibility: visible;
    }
    34% {
       visibility: hidden;
    }
    35% {
       visibility: hidden;
    }
    36% {
      visibility: visible;
    }
    to {
     visibility: visible;
    }
}

.down-arrow {
    position: relative;
    transform: rotate(-45deg);
    box-shadow: 32px -32px 0px white inset;
    width: 80px;
    height: 80px;
    margin: 32px 0;
    transition: transform 0.2s;
}

.down-arrow:hover {
    transform: rotate(-45deg) scale(1.1);
}

.down-arrow:nth-child(2) {
    top: -112px;
    animation: arrow-grow 1s forwards linear, arrow-flicker 10s infinite forwards;
    animation-delay: 1s;
}

.down-arrow:nth-child(3) {
    top: -224px;
    animation: arrow-grow 1s forwards linear;
    animation-delay: 2s;
}

h1 {
    animation: text-fade var(--pulse-duration) infinite forwards var(--timing-function),
    blur-loop var(--pulse-duration) infinite forwards var(--timing-function);
    font-size: 48px;
}

/* About Me Page */

h2 {
    margin: 0;
    color: white;
    font-size: 2em;
}

h2:nth-child(1) {
    margin-left: 0px;
}

h2:nth-child(2) {
    margin-left: calc(var(--h2-offset) * 1);
}

h2:nth-child(3) {
     margin-left: calc(var(--h2-offset) * 2);
}

h2:nth-child(4) {
     margin-left: calc(var(--h2-offset) * 3);
}

h2 > a {
    color: white;
    font-size: 2.5em;
    text-decoration: underline;
}

#food-container {
    text-align: center;
    font-size: 0.75em;
    margin-top: 32px;
    margin-right: 32px;
    float: right;
}

#food {
    margin-top: 8px;
    width: 300px;
}

@media (max-width: 575.98px /*var(--small-device-width)*/) { 
    h2 {
        font-size: 1.5em;
    }

    h2 > a {
        font-size: 2em;
    }

    #about-me-page {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    #food-container {
        margin-right: 0;
    }

    #food {
        width: 200px;
    }
}

/* Stretch Page */

@keyframes grow {
    from {
        width: 0px;
        height: 0px;
    }
    to {
        width: var(--full-dim);
        height: var(--full-dim);
    }
}

.stretch {
    width: 0px;
    height: 0px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgb(63, 155, 101);
    border-radius: 10000px;
    animation: grow 3s ease-in-out forwards reverse;
}
